@import '../../style-config/variable.scss';
@import '../../style-config/mixins.scss';



.box-about {
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    gap: 3rem;
    color: $white;
    padding: 2rem;
    @include tablet-screen {
        grid-auto-flow: row;
    }
    h1 {
        color: #c4343c;
        letter-spacing: 4px;
        font-size: 14px;
    }
    .col-desc {
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }       
        .title {
            padding-bottom: 1rem;
        }
        .quote {
            position: relative;
        }
        .quote:before {
            content: '';
            display: block;
            width: 28px;
            height: 24px;
            background: url(https://www.attekita.com/wp-content/themes/attekita/assets/quote-left.png) no-repeat;
            position: absolute;
            top: -10px;
            left: -40px;
        }
        .quote:after {
            content: '';
            display: block;
            width: 28px;
            height: 23px;
            background: url(https://www.attekita.com/wp-content/themes/attekita/assets/quote-right.png) no-repeat;
            position: absolute;
            bottom: -12px;
            right: -28px;
        }
        .box-quote p {
            font-style: italic;
        }
        .autor {
            padding-top: 2rem;
            text-align: right;
        }

    .content-about h1 {
        font-size: 60px;
        font-weight: 300;
    }
        & p {
            font-size: 16px;
        }
    .profile {
        width: 320px;
        height: 320px;
        display: block;
        margin: auto;
        background: url(https://www.attekita.com/wp-content/themes/attekita/assets/profile-circle.png) no-repeat;
        background-size: 100% 100%;
        position: relative;
    }
        .thumb {
            width: 70%;
            height: 70%;
            border-radius: 100%;
            background: url(../../images/me.jpg) no-repeat;
            background-size: cover;
            background-position: left 35% top 50%;
            position: absolute;
            top: 15%;
            left: 15%;
        }
}

